<template>
  <div id="app">
    <div class="layout-header">
      <div class="logo">QIANKUN-MAIN</div>
      <ul class="sub-apps">
        <li
          :class="{ active: '/#/main/mainpage' === current }"
          @click="jumpToMsinpage()"
        >
          main-app
        </li>
        <li
          v-for="item in microApps"
          :class="{ active: item.activeRule === current }"
          :key="item.name"
          @click="goto(item)"
        >
          {{ item.name }}
        </li>
        <li :class="{ active: '/#/main/inpage' === current }" @click="jumpToApps">
          communicate btw sub apps
        </li>
        <li>
          <el-input
            size="mini"
            v-model="msg"
            placeholder="‰∏émicro-appÂ≠êÂ∫îÁî®ÈÄö‰ø°"
            @change="sendMessageToChildren"
          ></el-input>
        </li>
        <li>
          {{ childMsg || "üëâ Msg Receiving from micro-app..." }}
        </li>
      </ul>
    </div>
    <div id="subapp-viewport">
      <!-- ÂæÆÂ∫îÁî®ÈîÄÊØÅÂêéÔºå<router-view>Â∞Ü‰∏çÂ≠òÂú®ÔºåËøõËÄåÂØºËá¥ÂàáÊç¢Âõû‰∏ªÂ∫îÁî®Êó∂È°µÈù¢‰∏çÂä†ËΩΩÈóÆÈ¢ò -->
      <router-view></router-view>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import microApps from "./micro-app";
export default {
  name: "App",
  data() {
    return {
      microApps,
      current: "",
      msg: "",
      // childMsg: "",
    };
  },
  computed: {
    childMsg() {
      return this.$store && this.$store.state && this.$store.state.sendMsg;
    },
  },
  methods: {
    goto(item) {
      history.pushState(null, item.activeRule, item.activeRule); // Ê≤°ÂºïÂÖ•Ë∑ØÁî±ÔºåÊâÄ‰ª•‰∏çËÉΩÁî®Ë∑ØÁî±ÂàáÊç¢
      this.resetCurrentPath();
    },

    // Ë∑≥ËΩ¨‰∏ªÂ∫îÁî®È°µÈù¢
    jumpToMsinpage() {
      // history.pushState(null, "/main/mainpage", "/main/mainpage"); // Ê≤°ÂºïÂÖ•Ë∑ØÁî±ÔºåÊâÄ‰ª•‰∏çËÉΩÁî®Ë∑ØÁî±ÂàáÊç¢
      this.$router.push({
        path: "/main/mainpage",
      });
      this.resetCurrentPath();
    },
    jumpToApps() {
      this.$router.push({
        path: "/main/inpage",
      });
      this.resetCurrentPath();
    },
    sendMessageToChildren(v) {
      this.$actions.setGlobalState({
        msg: v,
      });
    },
    resetCurrentPath() {
      let path = `/${window.location.hash}`;
      this.current = path;
    },
  },
  created() {
    let path = `/${window.location.hash}`;
    if (this.microApps.findIndex((item) => item.activeRule === path) >= 0) {
      this.current = path;
    }
  },
};
</script>

<style>
html,
body {
  margin: 0 !important;
  padding: 0;
}
.layout-header {
  height: 50px;
  width: 100%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  line-height: 50px;
  position: relative;
}
.logo {
  float: left;
  margin: 0 50px;
}
.sub-apps {
  list-style: none;
  margin: 0;
  overflow: hidden;
}
.sub-apps li {
  list-style: none;
  padding: 0 20px;
  cursor: pointer;
  float: left;
}
.sub-apps li.active {
  color: #42b983;
  text-decoration: underline;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>


