import Vue from "vue";
import Vuex from "vuex";
import store from "./store";
import App from "./App.vue";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import {
  registerMicroApps,
  start,
  setDefaultMountApp,
  initGlobalState
} from "qiankun";
import microApps, { apps } from "./micro-app";
import microConfig from "./micro-config";
import router from "./router";

Vue.config.productionTip = false;
Vue.use(Vuex);
Vue.use(ElementUI);

let _store = new Vuex.Store(store);
Vue.prototype.$store = _store;

new Vue({
  router,
  _store,
  render: h => h(App)
}).$mount("#app");

// ‰∏ªÂ∫îÁî®Áä∂ÊÄÅÂàùÂßãÂåñ
// ÈÄöËÆØ
const actions = initGlobalState({
  mt: "init", // ÂàùÂßãÂåñstate
  msg: "",
  sendMsg: "",
  inpageMsg: ""
});
// Âú®È°πÁõÆ‰∏≠‰ªª‰ΩïÈúÄË¶ÅÁõëÂê¨ÁöÑÂú∞ÊñπËøõË°åÁõëÂê¨ÔºåÂú®ËøôÈáåÁõëÂê¨ÊòØ‰∏∫‰∫ÜÊñπ‰æø
actions.onGlobalStateChange((state, prev) => {
  console.log("main state change", state);
  _store.commit("updateMsg", state);
  _store.commit("updateSendMsg", state);
  _store.commit("updateInpageMsg", state);
});
// Â∞ÜactionÂØπË±°ÁªëÂà∞VueÂéüÂûã‰∏äÔºå‰∏∫‰∫ÜÈ°πÁõÆ‰∏≠ÂÖ∂‰ªñÂú∞Êñπ‰ΩøÁî®Êñπ‰æø
Vue.prototype.$actions = actions;

registerMicroApps(apps, microConfig);
// setDefaultMountApp(microApps[0].activeRule); // ÈªòËÆ§ÊâìÂºÄÁ¨¨‰∏Ä‰∏™Â≠êÈ°πÁõÆ
start();



